.image-tile {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 40px;

    @include ele(body) {
        display: flex;
        height: 150px; }

    @include ele(buttons) {
        margin-top: 10px; }

    @include ele(caption) {
        height: 50px;
        resize: none; }

    @include ele(controls) {
        position: absolute;
        top: 0;
        right: 0; }

    @include ele(form-section) {
        display: flex;
        flex-direction: column; }

    @include ele(image) {
        display: flex; }

    @include ele(input) {
        margin: 0 15px 0 5px;
        padding-bottom: 0; }

    @include ele(name) {
        display: flex;
        align-items: center;
        margin-bottom: 4px; }

    @include ele(right) {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 15px;
        width: 100%; }

    @include tiny() {

        @include ele(body) {
            flex-direction: column;
            height: auto; }

        @include ele(container) {
            justify-content: center; }

        @include ele(form-section) {
            margin-top: 10px; }

        @include ele(right) {
            width: auto;
            margin-left: 0; } } }
