.project {

    @include ele(display-header) {
        margin: 0;
        color: $primaryColor; }

    @include ele(display-item) {
        margin-left: 15px;
        padding-top: 5px; }

    @include ele(display-section) {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0; } }

    @include ele(display-subsection) {
        border-bottom: $border;
        padding: 10px 0;

        &:last-child {
            border-bottom: 0; } }

    @include ele(error) {
        text-align: center; }

    @include ele(half) {
        flex: 1 0;
        width: 700px;

        &:first-child {
            padding-right: 20px;
            border-right: $border; }

        &:last-child {
            padding-left: 20px; } }

    @include ele(half-header) {
        text-align: center;
        font-style: italic;
        margin: 0 0 20px; }

    @include ele(main) {
        position: relative;
        display: flex;
        min-width: 1150px; }

    @include wide() {

        @include ele(half) {
            width: auto;
            min-width: 500px;

            &:first-child {
                padding-right: 0; } }

        @include ele(main) {
            min-width: 850px; } }

    @include medium() {

        @include ele(display-section) {

            & .button:first-child {
                margin-right: $buttonSpace; } }

        @include ele(half) {

            &:first-child {
                border-right: 0; }

            &:last-child {
                display: none; } }

        @include ele(main) {
            width: $widthMedium;
            min-width: initial;
            margin: 25px auto 0; } }


    @include small() {

        @include ele(half) {
            min-width: initial; }

        @include ele(main) {
            width: $widthSmall; } }

    @include tiny() {

        @include ele(main) {
            width: $widthTiny; } } }

