$leftMargin: 15px;
$leftMarginMobile: 0;

.flash-message {
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 100;
    background-color: $grey;
    width: 350px;
    height: 80px;
    color: $white;
    @include card(1);

    @include mod(error) {
        background-color: $red; }

    @include mod(success) {
        background-color: $green; }

    @include ele(bottom) {
        display: flex;
        margin-left: $leftMargin; }

    @include ele(close) {
        position: absolute;
        top: 5px;
        right: 5px;
        color: $white;
        cursor: pointer; }

    @include ele(lines) {
        & .lines svg {
            height: 20px; }

        & svg g {
            fill: $white; } }

    @include ele(title) {
        font-size: 1.7em;
        margin-left: 10px; }

    @include ele(top) {
        display: flex;
        align-items: center;
        margin: 10px 0 10px $leftMargin; }

    @include small() {
        height: 50px;
        min-width: 200px;
        width: auto;
        padding: 5px 10px;
        bottom: 15px;
        right: 15px;

        @include ele(bottom) {
            margin-left: $leftMarginMobile; }

        @include ele(lines) {
            & .lines svg {
                height: 12px; } }

        @include ele(message) {
            font-size: .9em; }

        @include ele(title) {
            font-size: 1.2em; }

        @include ele(top) {
            margin: 5px 0 5px $leftMarginMobile; } } }

