.footer {
    display: flex;
    flex-direction: column;
    background-color: $white;
    height: 210px;
    margin-top: auto;
    color: $black;
    border-top: $border;

    @include ele(bottom) {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: auto;
        height: 30px;
        background-color: $black; }

    @include ele(bottom-link) {
        color: $white; }

    @include ele(brand) {
        font-size: 1.7em;
        font-family: $brandFont; }

    @include ele(larafolio) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 30px; }

    @include ele(link) {
        color: $primaryColor; }

    @include ele(links) {
        display: flex;
        justify-content: space-around;
        margin-top: 15px; }

    @include ele(logo) {

        & svg, svg g {
            height: 50px;
            width: 50px;
            fill: $primaryColor;

            // & rect:nth-child(3), rect:nth-child(7)
            //     fill: $white

            // & rect:nth-child(2), rect:nth-child(4), rect:nth-child(6), rect:nth-child(8),
 } } }            //     fill: lighten($primaryColor, 20)
