html, body {
    height: 100%;
    margin: 0;
    font-family: $bodyFont;
    color: $black; }

#app {
    height: 100%; }

.container {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    margin: auto;
    background-color: $offWhite; }

.content {
    margin: 20px 40px 50px;
    max-width: 1200px;
    align-self: center;

    @include medium() {
        margin: 20px auto 50px;
        width: 95%; }

    @include small() {
        margin: 70px auto 50px; } }

.top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 40px 0;

    @include ele(title-block) {
        display: flex;
        align-items: center; }

    @include ele(title) {
        margin: 0 0 0 10px; }

    @include medium() {
        margin: auto;
        width: $widthMedium;
        align-items: flex-start; }

    @include small() {
        width: $widthSmall;
        flex-direction: column;

        @include ele(section) {
            width: $widthSmall; }

        @include ele(title-block) {
            width: $widthSmall; } }

    @include tiny() {
        width: $widthTiny; } }

.placeholder, ::-webkit-input-placeholder {
    font-style: italic;
    color: $darkGrey;
    font-size: 1em;
    font-weight: normal; }

::-moz-placeholder {
    font-style: italic;
    color: $darkGrey;
    font-size: 1em; }

:-ms-input-placeholder {
    font-style: italic;
    color: $darkGrey;
    font-size: 1em; }

:-moz-placeholder {
    font-style: italic;
    color: $darkGrey;
    font-size: 1em; }

a {
    color: $green;
    text-decoration: none;
    transition: $hoverTransition;

    &:hover {
        text-decoration: underline; } }

[v-cloak] {
    display: none; }
