$horizontalSpace: 15px;
$verticalSpace: 25px;
$infoSpace: 10px;

$arrowWidth: 25px;

.dashboard {

    @include ele(arrows) {
        display: flex;
        flex-direction: column;
        justify-content: space-around; }

    @include ele(flex-medium) {
        display: flex; }

    @include ele(flex-tiny) {
        display: flex; }

    @include ele(icon) {
        display: flex;
        align-items: center; }

    @include ele(item) {
        display: flex;
        margin: $verticalSpace 0;
        padding-bottom: $verticalSpace;
        border-bottom: $border;

        &:last-child {
            margin-bottom: 0;
            border-bottom: 0; } }

    @include ele(info) {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-width: 220px;
        margin-right: $horizontalSpace; }

    @include ele(name) {
        margin: 0 0 $infoSpace; }

    @include ele(photo) {
        display: flex;
        margin-right: $horizontalSpace; }

    @include ele(type) {
        margin-bottom: $infoSpace; }

    @include ele(visibility) {
        margin-bottom: $infoSpace;

        & .nav__icon {
            cursor: pointer; } }

    @include ele(wrapper) {
        display: block; }

    @include medium() {

        @include ele(arrows) {
            width: $arrowWidth; }

        @include ele(description) {
            margin-top: $verticalSpace; }

        @include ele(flex-medium) {
            flex-direction: column;
            width: calc(100% - #{$arrowWidth}); }

        @include ele(wrapper) {
            min-width: $widthMedium;
            width: $widthMedium;
            margin: auto; } }

    @include small() {

        @include ele(wrapper) {
            width: $widthSmall; } }

    @include tiny() {

        @include ele(flex-tiny) {
            flex-direction: column; }

        @include ele(info) {
            flex-direction: row;
            justify-content: space-between;
            margin-right: 0; }

        @include ele(photo) {
            justify-content: center;

            & img {
                max-width: 100%; } }

        @include ele(wrapper) {
            width: $widthTiny; } } }
