.button {
  display: inline-block;
  padding: 10px;
  background-color: #c8c8c8;
  color: white;
  -webkit-appearance: none;
  border-style: none;
  border: none;
  border-radius: 1px;
  text-align: center;
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  font-family: 'Lato', sans-serif;
  min-width: 38px;
}

.button:hover {
  text-decoration: none;
}

.button:disabled {
  background-color: #c8c8c8;
  box-shadow: none;
}

.button:disabled:hover {
  background-color: #c8c8c8;
  cursor: default;
}

.button--primary {
  background-color: #4390ad;
}

.button--primary:hover {
  background-color: #357188;
}

.button--green {
  background-color: #49c965;
}

.button--green:hover {
  background-color: #33ac4d;
}

.button--red {
  background-color: #f44436;
}

.button--red:hover {
  background-color: #ea1d0d;
}

.button--secondary {
  background-color: white;
  color: #357188;
}

.button--secondary:hover {
  background-color: #e6e6e6;
}

.button--icon {
