.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 6em;
  background-color: white;
  border-bottom: 1px solid #4390ad;
}

.nav__brand {
  display: none;
}

.nav__center {
  justify-content: center;
}

.nav__center svg, .nav__center svg g {
  height: 30px;
  width: 30px;
  fill: #4390ad;
}

.nav__dropdown {
  position: relative;
  display: inline-block;
}

.nav__dropdown:hover .nav__dropdown-content {
  display: block;
}

.nav__dropdown:hover .nav__item {
  color: #4390ad;
}

.nav__dropdown-content {
  display: none;
  position: absolute;
  top: 2.5em;
  left: 1em;
  min-width: 150px;
  z-index: 10;
  background-color: white;
  color: #4390ad;
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
}

.nav__dropdown-item {
  display: flex;
  align-items: center;
  height: 40px;
  color: #4390ad;
  transition: all 0.2s;
}

.nav__dropdown-item:hover {
  background-color: #e6e6e6;
}

.nav__dropdown-item-text {
  padding: 0 1em;
}

.nav__icon {
  height: 16px;
  margin-right: .5em;
}

.nav__icon svg {
  height: 16px;
}

.nav__icon g {
  transition: all 0.2s;
  fill: #4390ad;
}

.nav__item {
  display: flex;
  align-items: center;
  color: #4390ad;
  padding: 0 1em;
  height: 50px;
  cursor: pointer;
  transition: all 0.2s;
}

.nav__item:hover {
  text-decoration: none;
  color: #357188;
}

.nav__item:hover svg, .nav__item:hover svg g {
  fill: #357188;
}

.nav__link:hover {
  text-decoration: none;
}

.nav__right {
  justify-content: flex-end;
}

.nav__section {
  display: flex;
  flex: 1 0;
}

.nav__toggle {
  display: none;
}

@media only screen and (max-width: 1000px) {
  .nav {
    position: absolute;
    top: 0;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    width: 150px;
    height: 100%;
    z-index: 100;
    border-bottom: 0;
    padding: 50px 0 0;
    box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  }
  .nav__brand {
    display: block;
    margin-top: 5px;
    font-family: "Nova Script", cursive;
    font-size: 1.2em;
  }
  .nav__center {
    order: 0;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .nav__dropdown-content {
    top: 0;
    left: 120px;
    box-shadow: 0 3px 12px rgba(0, 0, 0, 0.23), 0 3px 12px rgba(0, 0, 0, 0.16);
  }
  .nav__left {
    flex-direction: column;
    align-self: flex-start;
    order: 1;
  }
  .nav__right {
    flex-direction: column;
    align-self: flex-start;
    order: 2;
  }
  .nav__section {
    flex: 0 1 auto;
  }
  .nav__toggle {
    display: block;
    position: absolute;
    top: 5px;
    left: 0;
    cursor: pointer;
  }
  .nav__toggle svg, .nav__toggle svg g {
    fill: #5a5a5a;
    width: 25px;
    height: 25px;
  }
}

.slide-enter-active {
  animation: slide-in .3s;
}

.slide-leave-active {
  animation: slide-out .3s;
}

@keyframes slide-in {
  0% {
    transform: translateX(-160px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slide-out {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-160px);
  }
}

