.modal__mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity .3s ease;
}

.modal__wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal__container {
  position: relative;
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #f7f9f9;
  border-radius: 1px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all .3s ease;
}

.modal__header h3 {
  margin-top: 0;
  color: #5a5a5a;
  text-align: center;
}

.modal__body {
  margin: 20px 0;
  text-align: center;
}

.modal__footer {
  text-align: center;
}

.modal__close {
  position: absolute;
  top: 5px;
  right: 10px;
  text-align: right;
  color: #f44436;
  font-size: 1.5em;
  cursor: pointer;
}

.modal__close svg {
  height: 12px;
  width: 12px;
}

.modal__close svg g {
  fill: #f44436;
}

.modal__buttons {
  display: flex;
  justify-content: space-around;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

